import React, { Component } from "react";
class Tab4 extends Component {
  constructor(props) {
    super(props);
    {
      this.state = {
        file: "There! I'm",
        contactheading:
          "I'm available for freelance work.Drop me a line if you fancy a chat.",
        email: "shekharpareek30@gmail.com",
      };
    }
  }

  render() {
    return (
      <div className="discription-container">
        <div className="main-content">
          <div className="common_flex-box item_1">
            <p>&#x1F60A;{this.state.file}</p>
            <span>
              <a href="https://in.linkedin.com/in/shekhar-pareek-1b8178167">
                <button>send feedback</button>
              </a>
              <i class="fa-solid fa-pen"></i>
            </span>
          </div>
          <div></div>
          <div className="item_2">
            <h5> {this.state.contactheading}</h5>
            <div className="contact-info">
              <a href="https://mail.google.com/mail/u/0/#inbox">
                <h2>{this.state.email}</h2>
              </a>
            </div>
            <div className="social-handel">
              <div>
                <a href="">
                  <i class="fa-brands fa-linkedin"></i>
                </a>
              </div>
              <div>
                <a href="">
                  <i class="fa-brands fa-instagram"></i>
                </a>
              </div>
              <div>
                <a href="">
                  <i class="fa-brands fa-facebook"></i>
                </a>
              </div>
              <div>
                <a href="">
                  <i class="fa-brands fa-twitter"></i>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Tab4;
