import React, { Component } from "react";
class Userprofile extends Component {
  constructor(props) {
    super(props);
    {
      this.state = {
        name: "Shekhar Pareek",
        email: "shekhar.pareek@sunarctechnologies.com",
      };
    }
  }
  render() {
    return (
      <div className="section-1">
        <div className="img-container">
          <img src="https://avatars.githubusercontent.com/u/108127963?v=4"></img>
        </div>
        <div className="smile-face">
          <i class="custom-smily fa-sharp fa-solid fa-face-smile"></i>
        </div>
        <div className="content">
          <h4>{this.state.name}</h4>
          <p className="light-text">
            <a href="https://mail.google.com/mail/u/0/#inbox">
              {this.state.email}
            </a>
          </p>
          <p>{this.state.about}</p>

          <button>
            <b>Resume</b>
          </button>
        </div>
        <div className="following">
          <i class="light-text fa-solid fa-users"></i>
          <p>
            <b>43</b>
            <span className="light-text">Following.</span>
          </p>
          <i class="light-text fa-solid fa-users"></i>
          <p>
            <b>38</b>
            <span className="light-text">Followers.</span>
          </p>
        </div>
      </div>
    );
  }
}
export default Userprofile;
