import React, { Component } from "react";
import Tab1 from "./Tabs/Tab1.jsx";
import Tab2 from "./Tabs/Tab2.jsx";
import Tab3 from "./Tabs/Tab3.jsx";
import Tab4 from "./Tabs/Tab4.jsx";
import Userprofile from "./Userprofile.jsx";

class NavBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      list_1: "Home",
      list_2: "Aboutme",
      list_3: "Skills",
      list_4: "Contact",
      activeTab: "Home",
    };
    this.handleTab = this.handleTab.bind(this);
  }
  handleTab(tabName) {
    this.setState({ activeTab: tabName });
    // console.log(tabName);
  }

  render() {
    return (
      <div className="main-parent-container">
        <header></header>
        <div className="header-container">
          {/* <div className="Empty-container"></div> */}

          <div className="origin">
            <div className="user-info">
              <Userprofile />
            </div>
            <div className="User-description">
              <nav class="nav-bar">
                <ul>
                  <li
                    onClick={() => this.handleTab("Home")}
                    className={
                      this.state.activeTab === "Home" ? " active " : " "
                    }
                  >
                    {this.state.list_1}
                  </li>
                  <li
                    onClick={() => this.handleTab("Aboutme")}
                    className={
                      this.state.activeTab === "Aboutme" ? " active " : " "
                    }
                  >
                    {this.state.list_2}
                  </li>
                  <li
                    onClick={() => this.handleTab("Skills")}
                    className={
                      this.state.activeTab === "Skills" ? " active " : " "
                    }
                  >
                    {this.state.list_3}
                  </li>
                  <li
                    onClick={() => this.handleTab("Contact")}
                    className={
                      this.state.activeTab === "Contact" ? " active " : " "
                    }
                  >
                    {this.state.list_4}
                  </li>
                </ul>
              </nav>
              {this.state.activeTab == "Home" && <Tab1 />}
              {this.state.activeTab == "Aboutme" && <Tab2 />}
              {this.state.activeTab == "Skills" && <Tab3 />}
              {this.state.activeTab == "Contact" && <Tab4 />}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default NavBar;
