import React, { Component } from "react";
class Tab3 extends Component {
  constructor(props) {
    super(props);
    {
      this.state = {
        file: "There! I'm",
        gretting: "Skills in Technologies like:",
      };
    }
  }

  render() {
    return (
      <div className="discription-container">
        <div className="main-content">
          <div className="common_flex-box item_1">
            <p>&#x1F60A;{this.state.file}</p>

            <span>
              <a href="https://in.linkedin.com/in/shekhar-pareek-1b8178167">
                <button>send feedback</button>
              </a>
              <i class="fa-solid fa-pen"></i>
            </span>
          </div>
          <div></div>
          <div className="item_2">
            <h3> &#x270C;{this.state.gretting} </h3>
            <div className="Skill-container">
              <div>
                <i class="fa-brands custom-fa-html5 fa-html5"></i>
              </div>
              <div>
                <i class="fa-brands custom-fa-css3 fa-css3-alt"></i>
              </div>
              <div>
                <i class="fa-brands custom-fa-square-js fa-square-js"></i>
              </div>
              <div>
                <i class="fa-brands custom-fa-sass  fa-sass"></i>
              </div>
              <div>
                <i class="fa-brands custom-fa-react fa-react"></i>
              </div>
              <div>
                <i class="fa-brands custom-fa-bootstrap fa-bootstrap"></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Tab3;
